import React from "react";
import Power from "../media/images/intro/power_web.jpg";
import { useHistory } from "react-router-dom";
import {
  AwesomeButton,
  AwesomeButton2,
  BackgroundContainer,
  Container,
  ContainerRow,
  ScaleDownImg,
  FadeInText5,
  FadeInText8,
  FadeInText15noflex,
  FadeInText20,
  HalfContainer,
  HalfContainerText,
  ButtonContainer,
  Para,
} from "../styles/intro_style";

function Intro({setSkip}) {
  const history = useHistory();
  const route1 = () => {
    history.push("/intro2");
  };

  const route2 = () => {
    setSkip(true)
    history.push("/main");
  };
  return (
    <>
      <BackgroundContainer>
        <ContainerRow>
          <HalfContainer>
            <ScaleDownImg>
              <img
                style={{
                  width: "50vw",
                  height: "100vh",
                  objectFit: "cover",
                }}
                src={Power}
                alt="Powerful"
              />
            </ScaleDownImg>
          </HalfContainer>
          <HalfContainerText>
            <Container>
              <FadeInText5>
                <Para>Stop.</Para>
              </FadeInText5>
              <FadeInText8>
                <Para>Think For a Moment...</Para>
              </FadeInText8>
              <FadeInText15noflex>
                <Para>Why are you Here?</Para>
              </FadeInText15noflex>
            </Container>

            <FadeInText20>
              <AwesomeButton2 onClick={route1}>Continue...</AwesomeButton2>
            </FadeInText20>

            <ButtonContainer>
              <AwesomeButton onClick={route2}>Skip Intro</AwesomeButton>
            </ButtonContainer>
          </HalfContainerText>
        </ContainerRow>
      </BackgroundContainer>
    </>
  );
}

export default Intro;
