import React from "react";
import { ImageDiv, ImgRow, MemberTitle, MemberDescription, } from "../styles/about_us_style";
import { Card, Badge, Collapse } from "reactstrap";
function BoardMemberDescription({ member, selectedMember }) {
  return (
    <>
      <Collapse isOpen={selectedMember === member}>
        <Card style={{ boxShadow: "10px 10px 5px grey", padding: "5%" }}>
   <ImgRow>
            <ImageDiv src={selectedMember.image} alt={selectedMember.first_name} />
         </ImgRow>
          <MemberTitle>
            {selectedMember.first_name} {selectedMember.last_name}   {" "}
            &nbsp; <Badge>{selectedMember.role}</Badge>
          </MemberTitle>
          <MemberDescription>{selectedMember.description}</MemberDescription>
        </Card>
      </Collapse>
    </>
  );
}

export default BoardMemberDescription;
